<template>
  <div>
    <van-cell>
<!--      -->
      <template #title>
        <div class="title-box">
<!--           -->
          <span>{{ title }}</span>
<!--          -->
          <img :src="cover.images[0]" alt="" class="thumb" v-if="cover.type === 1">
        </div>

<!--        -->
        <div class="thumb-box" v-if="cover.type === 3">
          <img :src="item" alt="" class="thumb" v-for="(item , i) in cover.images" :key="i">
        </div>
      </template>

<!--      label-->
      <template #label>
        <div class="label-box">
          <span>{{mouther}} &nbsp;&nbsp;{{count}}&nbsp;&nbsp;{{time}}</span>
          <!--        -->
          <van-icon name="cross" />
        </div>
      </template>
    </van-cell>
  </div>
</template>

<script>
export default {
  name: "Activeinfo",
  props:{
    //
    title:{
      type:String,
      default:''
    },
    //
    mouther:{
      type:String,
      default: ''
    },
    //
    count:{
      type:String,
      default:0
    },
    //
    time:{
      type:String,
      default:''
    },
    //
    cover:{
      type:Object,
      default: () =>{
        return { type:0 }
      }
    }
  }
}
</script>

<style scoped>
.label-box{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.thumb{
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}
.title-box{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.thumb-box{
  display: flex;
  justify-content: space-between;
}
</style>