<template>
  <div class="home-container">
    <van-nav-bar title="" :fixed="true"/>
<!------------------------------------------------------------------------->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>


<!--    ------------------------------------------------------------------->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
<!--    ---------------------------------------------------------------->
      <van-list
          v-model="loading"
          :finished="finished"
          finished-text="~~"
          @load="onLoad">
        <activeinfo v-for="item in arrlist"
                    :key="item.id"
                    :title="item.title"
                    :mouther="item.aut_name"
                    :count="item.comm_count"
                    :time="item.pubdate"
                    :cover="item.cover">
        </activeinfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
// axios 
import $http from "../../utils/request";

// Aciveinfo  
import Activeinfo from "../../components/activeinfo/Activeinfo";
export default {
  name: "Home",
  components: {Activeinfo},
  comments:{
    Activeinfo
  },
  data(){
    return{
      //
      page: 1,

      //
      limit:10,

      //
      arrlist:[],

      // loading  true  load 
      //  loading  true  false
      loading:true,

      //,  finished  true
      finished:false,

      //
      isLoading:false
    }
  },
  //--------------------------------------------------------------------
  created() {
    this.inithttp()
  },
  //-------------------------------------------------------------------
  methods:{
    //
    async inithttp(isrefresh){
      const {data:res} = await $http.get('/articles',{
        params:{
          _page:this.page,
          _limit:this.limit
        }
      })
      //------------------------------------------------
      if (isrefresh) {
      //
        //this.arrlist = []
        this.arrlist = [...res, ...this.arrlist]
        this.isLoading = false
      }else{
      //
        //
        //this.arrlist = []
        this.arrlist = [...this.arrlist , ...res]
        this.loading = false
      }
      //----------------------------------------------------
      //,
      if(res.length == 0){
        this.finished = true
      }
      // console.log(this.arrlist)
    },

 //---------------------------------------------------------------------------
    //
    // onload 
    onLoad() {
      console.log('onload')
      //1. +1
      this.page++
      //2.
      this.inithttp()
    },

  //--------------------------------------------------------------------------
    //
    onRefresh(){
      console.log('')
      //1. +1
      this.page++
      //2.
      this.inithttp(true)
    }
  }
}
</script>

<style scoped>
.home-container{
  padding: 46px 0 50px 0;
}
.van-nav-bar{
  background-color: aqua;
}
.van-nav-bar__title{
  color: white;
}
/**/
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
/**/
</style>